.footContainer {
  width: 100%;
  margin: 0 auto;
  padding-top: 5%;
  color: #fff;
}

.foot {
  font-size: 15px;
  background-color: #000;
  padding-bottom: 4%;
  width: max-content;
  overflow-x: hidden;
}

.foot2 {
  display: flex;
  flex-wrap: wrap !important;
  width: 98.9vw;
  /* min-width:1200px; */
  justify-content: space-around;
  border-bottom: 1px solid #4d4d4d;
}

.foot3 {
  width: 98.9vh;
}

.uList li {
  font-size: 17px;
  text-transform: capitalize;
  padding: 0;
  list-style-type: none;
  display: block;
  text-align: left;
}

.uList li:hover {
  cursor: pointer;
  font-weight: bold;
}

.head {
  color: gray;
  font-size: 15px;
  text-transform: uppercase;
  font-weight: 600;
}

a {
  color: #fff;
  text-decoration: none;
}
.uList {
  padding: 0%;
  line-height: 1.5;
}
.pflex {
  display: flex;
  flex-direction: column;
  border: 2px solid #fff;
  border-radius: 10px;
  flex-wrap: wrap;
  margin: 10% 0;
}

.flex {
  display: flex;
  justify-content: space-between;
}

.fddf {
  flex-basis: 20%;
}

.fab {
  margin-left: 10%;
  font-size: 24px;
  transition: 0.5 all;
}

.fab:hover {
  transform: scale(1.5);
  cursor: pointer;
}

@media only screen and (max-width: 1200px) {
  .foot2 {
    justify-content: space-between;
  }
  
  .head {
    margin-left: 2%;
    width: 150px;
  }
  /* .foot {
  } */
}
